.horizontalOverflow{
  white-space: nowrap;
  overflow-x: auto;
}


.autoSizeImage{
  width: auto;
  height: auto;
  max-height: 400px;
  max-width: 100%;
}

.autoSizeMultipleImage{
  max-height: 440px;
  max-width: 90%;
}

.mediaCarousel{
  color: #0b4e6c;
  transition: color 0.3s ease;
}

.mediaCarousel:hover{
  color: #f59e24;
}


.mediaCarousel::-webkit-scrollbar-thumb {
  border: 8px solid;
  border-radius: 10px;
}

.mediaCarousel::-webkit-scrollbar {
  height: 8px;
}

.mediaCarousel::-webkit-scrollbar-track {
  border-radius: 5px;
}

.darkMediaCarousel {
  color: #495057;
}
.darkMediaCarousel:hover {
  color: #f59e24;
}

.imageMargin {
  margin-left: 5px
}

.multipleImage {
  margin-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 20px;
}
.multipleImageIOS {
  margin-top: 20px;
  margin-bottom: 5px;
}